<template>
	<div id="deliveryer-power">
		<public-header title="配送权限"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell title="配送权限" />
				<van-cell title="外卖单" class="border-0px">
					<iswitch
						:value="deliveryer.is_takeout"
						slot="right-icon"
						text-open="ON"
						text-close="OFF"
						@change="jsToggleSwitch"
						keys="deliveryer.is_takeout"
					>
					</iswitch>
				</van-cell>
				<van-cell title="跑腿单" class="border-0px">
					<iswitch
						:value="deliveryer.is_errander"
						slot="right-icon"
						text-open="ON"
						text-close="OFF"
						@change="jsToggleSwitch"
						keys="deliveryer.is_errander"
					>
					</iswitch>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">允许配送员取消订单（外卖单）</div>
					<iswitch
						:value="deliveryer.perm_cancel.status_takeout"
						slot="right-icon"
						text-open="ON"
						text-close="OFF"
						@change="jsToggleSwitch"
						keys="deliveryer.perm_cancel.status_takeout"
					>
					</iswitch>
				</van-cell>
				<van-cell>
					<div slot="title">允许配送员取消订单（跑腿单）</div>
					<iswitch
						:value="deliveryer.perm_cancel.status_errander"
						slot="right-icon"
						text-open="ON"
						text-close="OFF"
						@change="jsToggleSwitch"
						keys="deliveryer.perm_cancel.status_errander"
					>
					</iswitch>
				</van-cell>
				<van-cell>
					<div slot="title">允许配送员转单（外卖单）</div>
					<iswitch
						:value="deliveryer.perm_transfer.status_takeout"
						slot="right-icon"
						text-open="ON"
						text-close="OFF"
						@change="jsToggleSwitch"
						keys="deliveryer.perm_transfer.status_takeout"
					>
					</iswitch>
				</van-cell>
				<van-cell>
					<div slot="title">允许配送员转单（跑腿单）</div>
					<iswitch
						:value="deliveryer.perm_transfer.status_errander"
						slot="right-icon"
						text-open="ON"
						text-close="OFF"
						@change="jsToggleSwitch"
						keys="deliveryer.perm_transfer.status_errander"
					>
					</iswitch>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">配送员每天最多可转外卖单数</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model="deliveryer.perm_transfer.max_takeout"><span>单</span>
						</div>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">配送员每天最多可转跑腿单数</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model="deliveryer.perm_transfer.max_errander"><span>单</span>
						</div>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">配送员每天最多可抢外卖单数</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model="deliveryer.collect_max_takeout"><span>单</span>
						</div>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">配送员每天最多可抢跑腿单数</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model="deliveryer.collect_max_errander"><span>单</span>
						</div>
					</div>
				</van-cell>
			</van-cell-group>
			<div class="padding-15">
				<van-button size="normal" class="bg-primary font-16" block @click="onSubmit">提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				id: 0,
				showPreLoading: true,
				deliveryer: {
					is_takeout: 0,
					is_errander: 0,
					perm_cancel: {
						status_takeout: 0,
						status_errander: 0,
					},
					perm_transfer: {
						status_takeout: 0,
						status_errander: 0,
						max_takeout: 0,
						max_errander: 0,
					},
					collect_max_errander: 0,
					collect_max_takeout: 0
				}
			}
		},
		components: {
			publicHeader,
			iswitch
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					autoAssign: true,
					variable: 'deliveryer',
					url: 'plateform/deliveryer/plateform/power',
					data: {
						id: this.id
					}
				});
			},
			jsToggleSwitch(res) {
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			},
			onSubmit() {
				var that = this;
				setData({
					vue: that,
					url: 'plateform/deliveryer/plateform/power',
					data: {
						id: that.id,
						deliveryer: that.deliveryer
					},
					message: '配送员权限设置成功',
					redirect: that.util.getUrl({path: '/pages/deliveryer/guide', query: {id: that.id}})
				});
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = this.$route.query.id;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#deliveryer-power .iswitch{
		height: 24px;
	}
</style>


